body{
    background-image: linear-gradient(0deg, #182848, #2980b9)
}

@keyframes AnimateColony1 {
    from{
        left: -30%;
        top: 20%;
    }
    to{
        left: 100%;
        top:40%;
    }
}
@keyframes AnimateColony2{
    from{
        left: -30%;
        top: 80%;
    }
    to{
        left: 100%;
        top:70%;
    }
}
@keyframes AnimateColony3 {
    from{
        left: -10%;
        top: 30%;
    }
    to{
        left: 100%;
        top: 10%;
    }
}
#colony1{
    position: fixed;
    z-index: -1;
    width: 20%;
    height:fit-content;
    /* opacity: 0.5; */
    animation: AnimateColony1 30s linear infinite;
}
#colony1 div{
    width: 3.25% inherit;
}
#colony2{
    position: fixed;
    z-index: -1;
    width: 20%;
    height: fit-content;
    /* opacity: 0.5; */
    animation: AnimateColony2 20s linear infinite;
}
#colony3{
    position: fixed;
    z-index: -1;
    width: 20%;
    height: fit-content;
    /* opacity: 0.5; */
    animation: AnimateColony2 25s linear 5s infinite;
}
