ul{
    list-style: none;
}
img{
    display: block;
    width: 8em;
    margin-left: auto;
    margin-right: auto;
}
.social-list{
    display: inline-block;
    text-align: center;
    padding-bottom: 1rem;
    color: pink;
}
@media only screen and (max-width:825px) {
    .social-list{
        width: 50%;
    }
}
@media only screen and (min-width:826px) {
    .social-list{
        width: 33%;
    }
}