/* background #181944
text #8771CB 
text rectified #D0C7EA*/


.container{
  /* background-color: #1d3334;
  color: #3C9DCD; */
  background-color: #181944;
  color: #D0C7EA;
  margin: auto;
  margin-top: 1rem;
  margin-bottom: 1rem;
  border: #3C9DCD dashed 1px;
  border-radius: 0.5rem;
  padding: 1rem;
}
header{
  border-bottom: #3C9DCD solid 1px;
  border-radius: 0.5rem;
  padding: 1em 1em 0.5em 1em;
}
h3, h4{
  display:inline-block;
  width: 50%;
  padding-left: 1em;
  padding-right: 0.5em;
}
header h4{
  text-align: end;
}
article{
  padding: 1rem;
}
.section-1{
  display:flex;
  margin:0.5rem;
}
.profile-img{
  width: 10em;
  height: 10em;
  margin-right: 1rem;
  border-radius: 0.5rem;
  border-bottom: #3C9DCD solid 1px;
  border-right: #3C9DCD solid 1px;
  display: inline;
  align-self: center;
}
.profile-text{
  display: inline;
  width: 60%;
  margin-left: 1em;
}
.profile-text p{
  margin-top: 1rem;
  text-align: justify;
}
.section-2{
  margin: 1rem 0.5rem;
  border: #3C9DCD solid 1px;
  border-radius: 0.5rem;
  padding: 0.5rem;
}
Link{
  text-decoration: none;
}
.section-3{
  margin: 0.5rem;
}

footer{
  padding: 1rem;
  text-align: center;
  line-height: 1.5rem;
}
.author{
  text-align:end;
}
.author a{
  color: pink;
  text-decoration: underline wavy pink;
}

@media only screen and (min-width:1301px) {
  .container{
    width: 40%;
    height: 40%;
  }
}
@media only screen and (max-width:1300px) {
  .container{
    width: 50%;
    height: 50%;
  }
}
@media only screen and (max-width:825px){
  .container{
    width: 75%;
    height: 75%;
  }
  .profile-img{
    width: 8em;
    height: auto;
  }
}
@media only screen and (max-width:600px){
  .container{
    width: 90%;
    height: 90%;
  }
}